function RoundIconCards({name,src}){
    return (
        <div className="Images-with-round d-inline-block p-2" width="10px"> 
        <img className="img-fluid rounded-circle" src={src} width='100px' height='100px' alt="Chania" />
        <p>{name}</p>
      </div>
    )
}
export default RoundIconCards;